.schedules-inventory {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    background-color: var(--background);
    position: fixed;
    left: -100vw;
    transition: 300ms;
    overflow-y: auto;
    z-index: 2;
}

#schedules-inventory-active {
    left: 0;
}

.schedules-inventory::-webkit-scrollbar {
    display: none;
}

.schedules-inventory .loading-holder {
    position: fixed;
}

.schedules-inventory .info {
    z-index: 4;
}

.schedules-inventory .x-button {
    background-color: transparent;
    border: none;
    position: fixed;
    top: 25px;
    right: 25px;
    z-index: 2;
}

.schedules-inventory .x-button img {
    height: 25px;
    width: 25px;
    object-fit: contain;
}

.schedules-inventory h2 {
    height: 75px;
    width: 100%;
    color: var(--red);
    font-size: 28px;
    text-align: center;
    font-family: Roboto-Bold;
    background-color: var(--background);
    border-bottom: 1px solid white;
    position: sticky;
    top: 0;
    padding: 20px;
    z-index: 1;
}

.schedules-inventory .list {
    height: auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    flex-grow: 1;
    padding: 20px 0;
    overflow-y: visible;
}

.schedules-inventory .list strong {
    color: grey;
    font-size: 18px;
    font-family: Roboto-Thin;
    font-style: italic;
}

.schedules-inventory .list .medication {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;
    padding: 0 20px;
}

.schedules-inventory .list .medication img {
    height: 40px;
    width: 40px;
    object-fit: contain;
}

.schedules-inventory .list .medication .info-holder {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
}

.schedules-inventory .list .medication .info-holder strong {
    color: white;
    font-size: 18px;
    font-family: Roboto-Regular;
    font-style: normal;
    margin: 0;
}

.schedules-inventory .list .medication .info-holder span {
    color: var(--red);
    font-size: 15px;
    font-family: Roboto-Light;
    font-style: italic;
}

.schedules-inventory .list .medication .info-holder .info-inner-holder {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
}

.schedules-inventory .list .medication .info-holder .info-inner-holder p {
    color: white;
    font-size: 12px;
    font-family: Roboto-Regular;
    margin: 0;
}

.schedules-inventory .list .medication .info-holder .info-inner-holder p span {
    font-size: 12px;
}

.schedules-inventory .create-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--red);
    border: none;
    border-radius: 50%;
    position: sticky;
    bottom: 15px;
    padding: 10px;
}

.schedules-inventory .create-button img {
    height: 40px;
    width: 40px;
    object-fit: contain;
}